body {
	background: #F0E7D8;
	text-align: center;
}

.container {
	max-width: 1200px;
	padding: 0 20px;
	margin: 0 auto;
}

.global-header {
	background: #BA6E6E;
	overflow: hidden;
	margin-bottom: 20px;
	padding: 10vh 0;
	transition: all 0.5s;
}

.global-header.slim {
	padding: 5vh 0;
}

h1 {
	color: #F0E7D8;
	font-size: 30px;
	font-size: 1.875rem;
	text-transform: uppercase;
	font-family: 'Lato', sans-serif;
	font-weight: 800;
	margin-bottom: 0;
	margin-top: 0;
}

.subtitle {
	color: #F0E7D8;
	font-size: 18px;
	font-size: 1.125rem;
	text-transform: capitalize;
	text-align: center;
	font-family: 'Lato', sans-serif;
	margin-bottom: 0;
}

p.query {
	line-height: 30px;
	font-size: 18px;
	font-size: 1.125rem;
}

p.query span {
	display: inline-block;
}

.small {
	font-size: 14px;
	font-size: 0.875rem;
}

.loadtime {
	width: 50px;
}

/*
 * Standard clearfix for clearing floats
 */

.clearfix:before,
.clearfix:after {
	content: " ";
	display: table;
}

.clearfix:after {
	clear: both;
}

/*
 * Pull content to the left
 */

.pull-left {
	float: left;
}

/*
 * Pull content to the right
 */

.pull-right {
	float: right;
}

/*
 * Align text center
 */

.text-align-center {
	text-align: center;
}

/*
 * Convert text to uppercase
 */

.uppercase {
	text-transform: uppercase;
}

/*
 * Convert text to lowercase
 */

.lowercase {
	text-transform: lowercase;
}

/*
 * Hide visually and from screen readers:
 */

.hidden {
	display: none !important;
}

/*
 * Hide only visually, but have it available for screen readers:
 * http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
 */

.visuallyhidden {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

/*
 * Extends the .visuallyhidden class to allow the element
 * to be focusable when navigated to via the keyboard:
 * https://www.drupal.org/node/897638
 */

.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto;
}

/*
 * Hide visually and from screen readers, but maintain layout
 */

.invisible {
	visibility: hidden;
}

@media only screen and (min-width: 768px) {

.global-header {
	margin-bottom: 40px;
	padding: 20vh 0;
}

p.query {
	font-size: 22px;
	font-size: 1.375rem;
}

}

@media only screen and (min-width: 768px) and (min-height: 480px) {

h1 {
	font-size: 40px;
	font-size: 2.5rem;
}

.subtitle {
	font-size: 20px;
	font-size: 1.25rem;
}

}

